header {
    background: url(../images/header1.png) top/cover;
}

.library {
    background: url(../images/header2.png) center/cover no-repeat;
}

.nav-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: var(--white);
    padding: 35px 20px 20px 20px;
}

.nav-logo {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    line-height: 35px;
    font-size: 30px;
    font-weight: 500;
    text-transform: capitalize;
    cursor: pointer;
}

.nav-logo .icon {
    width: 24px;
    height: 24px;
}

.nav-list {
    display: flex;
    gap: 20px;
    font-size: 12px;
    font-weight: 500;
    line-height: 14.06px;
    text-transform: uppercase;
}

.nav-link::after {
    content: "";
    display: block;
    width: 0;
    height: 2px;
    background-color: var(--orange);
    transition: width 250ms ease-in-out;
}

.nav-link:hover::after {
    width: 100%;
}

.active::after {
    content: "";
    display: block;
    width: 100%;
    height: 2px;
    background-color: var(--orange);
}

.preload {
    display: none;
}

.nav-form {
    display: flex;
    justify-content: center;
    margin-top: 35px;
    padding-bottom: 70px;
}

.nav-input {
    width: 50%;
    padding: 8px;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid var(--white);
    color: var(--white);
}

.del-input {
    content: "\00d7";
    display: block;
    position: absolute;
    top: 135px;
    right: 36.5%;
    font-size: 20px;
    font-weight: 300;
    color: white;
    cursor: pointer;
}

.nav-input:focus,
.nav-button:focus {
    outline: none;
}

.nav-button {
    position: relative;
    top: 0;
    right: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
}

.nav-button .icon {
    width: 28px;
    height: 28px;
    color: var(--white);
}

.ml-buttons {
    display: flex;
    justify-content: center;
    gap: 7px;
    margin-top: 35px;
    padding-bottom: 60px;
}

.ml-button {
    width: 135px;
    height: 45px;
    background-color: transparent;
    border: 1px solid var(--white);
    border-radius: 4px;
    text-transform: uppercase;
    font-size: 12px;
    line-height: 16px;
    color: var(--white);
}

.ml-button:hover,
.ml-button.active,
.ml-button:active,
.ml-button:focus {
    cursor: pointer;
    background-color: var(--orange);
    box-shadow: 0 0 15px 4px var(--orange);
    border: 1px solid transparent;
}

/* MEDIA QUERIES */
@media (max-width: 767px) {
    .container {
        width: 320px;
        height: 230px;
    }

    .nav-logo {
        visibility: hidden;
        font-size: small;
    }

    .icon {
        visibility: visible;
    }

    .nav-list {
        gap: 30px;
        margin-right: 20px;
    }

    .nav-bar {
        margin-left: 20px;
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .container {
        width: 768px;
    }
}

@media (min-width: 1024px) {
    .container {
        width: 1024px;
    }
}